<template>
  <v-layout justify-center>
    <v-dialog v-model="showAddForm" persistent >
      <v-card>
        <v-card-title>
          <span class="headline"> Add User</span>
        </v-card-title>
        <v-card-text>
          <v-container grid-list-md>
            <v-layout wrap>
              <v-flex xs12 sm6 md4>
                <v-text-field v-model="addUserForm.empName" label="Employee Name" required></v-text-field>
              </v-flex>
              <v-flex xs12 sm6 md4>
                <v-text-field v-model="addUserForm.empId" label="Employee ID" required></v-text-field>
              </v-flex>
              <v-flex xs12 sm6 md4>
                <v-text-field v-model="addUserForm.email" label="Employee Email" required></v-text-field>
              </v-flex>
              <v-flex xs12 sm6 md4>
                <v-text-field v-model="addUserForm.phone" label="Employee Phone" required></v-text-field>
              </v-flex>
              <v-flex xs12 sm6 md4>
                <v-text-field v-model="addUserForm.empLevel" label="Employee Level" required></v-text-field>
              </v-flex>
              <v-flex xs12 sm6 md4>
                <v-text-field v-model="addUserForm.office" label="Employee office" required></v-text-field>
              </v-flex>
              <v-flex xs12  sm6 md6>
                <v-text-field v-model="addUserForm.team" label="Employee Team" required></v-text-field>
              </v-flex>
              <v-flex xs12 sm6 md6>
                <v-text-field v-model="addUserForm.department" label="Employee Department" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field v-model="addUserForm.organization" label="Employee Organizations (each organization splite with english , )" required></v-text-field>
              </v-flex>

              <!-- <v-flex xs12 sm6 md4>
                <v-text-field
                  v-model="Legal last name*"
                  hint="example of persistent helper text"
                  persistent-hint
                  required
                ></v-text-field>
              </v-flex> -->
              <v-flex xs12>
                <v-text-field v-model="addUserForm.memberShips" label="Employee Memberships (each member splite with english , )" required></v-text-field>
              </v-flex>
              <!-- <v-flex xs12 sm6>
                <v-select
                  :items="['0-17', '18-29', '30-54', '54+']"
                  v-model="Age*"
                  required
                ></v-select>
              </v-flex>
              <v-flex xs12 sm6>
                <v-autocomplete
                  :items="['Skiing', 'Ice hockey', 'Soccer', 'Basketball', 'Hockey', 'Reading', 'Writing', 'Coding', 'Basejump']"
                  v-model="Interests"
                  multiple
                ></v-autocomplete>
              </v-flex> -->
            </v-layout>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="cancelAdd">CANCEL</v-btn>
          <v-btn color="blue darken-1" text @click="addUser">ADD</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-layout>
</template>
<script>
  export default {
    props:{
      showAddForm:Boolean,
    },
    data(){
      return{
        addUserForm:{},
      }
    },
    methods:{
      cancelAdd(){
        this.$parent.showAddForm = false;
        this.addUserForm = {};
      },
      addUser(){
        let self = this;
        self.addUserForm.organization = self.addUserForm.organization.split(",");
        self.addUserForm.memberShips = self.addUserForm.memberShips.split(",");
        self.$axios.post("/api/employee",self.addUserForm).then(function(resp){
          if (resp.data.success) {
            self.cancelAdd();
            self.$parent.loadData();
          }
        }).catch(function(err){
          alert(err);
        })
      }
    }
  }
</script>
