<script>
import add from '../components/add.vue'
export default {
  components: {
    add
  },
  data() {
    return {
      inputVal: '',
      showAddForm: false,
      filteredData: [],
      totalCount: 0,
      rowsPerPageItems: [4, 8, 12],
      pagination: {
        rowsPerPage: 4
      },
      dbData: [
      //   {
      //   empName: 'a',
      //   empId: '009',
      //   email: 'er',
      //   phone: '090',
      //   empLevel: 'r',
      //   organization: ['1', '2'],
      //   memberShips: ['2', '3']
      // }
    ]

    }
  },
  watch: {
    inputVal() {
      return this.dataFilter();
    }
  },
  computed: {
    tableData() {
      this.totalCount = this.filteredData.length;
      return this.filteredData;
    }
  },

  methods: {
    loadData() {
      // this.dataFilter();
      let self = this;
      self.$axios.get("/api/employees").then(function(resp) {
        if (resp.data.success) {
          self.dbData = resp.data.rst;
          self.totalCount = self.dbData.length;
          self.dataFilter();
        }
      }).catch(function(err) {
        alert(err)
      })
    },
    dataFilter() {
      let self = this;
      let tempArr = self.dbData;
      let filterArr = [];
      if (self.inputVal === "") {
        filterArr = tempArr;
      } else {
        tempArr.forEach(t => {
          if (t.empName.startsWith(self.inputVal)) {
            filterArr.push(t)
          }
        });
      }
      self.filteredData = filterArr;
    },
  },
  mounted() {
    this.loadData();
  }

}
</script>
